import React, { useEffect, useState } from 'react';
import axios from 'axios';
import {useLocation, useNavigate} from 'react-router-dom';
import '../../../css/boardDetail.css';
import ReactQuill from "../../../js/ReactQuill";


export default function View() {
    const [board, setBoard] = useState(null);
    const [updateBtn, setUpdateBtn] = useState(false);
    const freeBoardNo = useLocation().state; //  page   freeBoardNo
    const navigate = useNavigate();

    const getBoard = async () => {
        //  page  data    console  
        const res = await axios.get('/freeBoard/view', {
            params : {
                freeBoardNo : freeBoardNo
            }
        })
        if (res.data) {
            setBoard(res.data);
        }
    };

    useEffect(() => {
        getBoard();
    }, []);

    //  
    const updatePost = () => {
            setUpdateBtn(true);
    }

    //  
    const deletePost = async () => {
        if(window.confirm(" ?")){
            const res = await axios.put('/freeBoard/delete',null, {
                params:{
                    freeBoardNo:freeBoardNo
                }
            })
                .then((navigate('/community/freeBoard/list')))
                .catch((error)=> console.log("  "))
        }
    }

    return (
        <>
            {
                updateBtn ?
                    <article className="mt-32 ml-32 mr-32">
                        <ReactQuill updateBtn={updateBtn}/>
                    </article>
                    :
                    <>
                    {board ? (
                            <div className="container" style={{marginTop:"10rem", marginLeft:"6rem"}}>
                                <div className="header">
                                    <div className="text-black text-2xl my-3">{board.freeTitle}</div>
                                    <div> : {board.memberId}</div>
                                    <div> : {new Date(board.registerTime).toLocaleString()}
                                        <span className="float-right mr-4">: {board.hits}</span>
                                    </div>
                                </div>
                                <div className="content" dangerouslySetInnerHTML={{__html:board.freeContent}}/>
                                <div className="actions">
                                    {/* TODO
                              */}
                                    <button className="bg-blue-500 text-white font-bold" onClick={updatePost}></button>
                                    <button className="bg-red-500 text-white font-bold" onClick={deletePost}></button>
                                    <button onClick={()=>navigate("/community/freeBoard/list")} className="bg-gray-500 text-white font-bold"></button>
                                </div>
                                <div className="comment-section">
                                    <textarea placeholder=" ."></textarea>
                                    <div className="comment-actions">
                                        <input type="text" placeholder="" />
                                        <button> </button>
                                    </div>
                                </div>
                            </div>
                        ) : (
                            <p>Loading...</p>
                        )}
                    </>
            }


        </>
    );
};
